
.spinner{position:fixed;z-index:999;transition:opacity .15s ease;animation:rotator 1.4s linear infinite;animation-play-state:paused;right:50%;top:20%;margin-right:-34px
}
.spinner.show{animation-play-state:running
}
.spinner.v-enter,.spinner.v-leave-active{opacity:0
}
.spinner.v-enter-active,.spinner.v-leave{opacity:1
}
@keyframes rotator{
0%{transform:scale(0.5) rotate(0deg)
}
100%{transform:scale(0.5) rotate(270deg)
}
}
.spinner .path{stroke:#42b983;stroke-dasharray:126;stroke-dashoffset:0;transform-origin:center;animation:dash 1.4s ease-in-out infinite
}
@keyframes dash{
0%{stroke-dashoffset:126
}
50%{stroke-dashoffset:63 transform rotate(135deg)
}
100%{stroke-dashoffset:126 transform rotate(450deg)
}
}



